.committees {
    background-image: url('../../assets/committees_pittU.jpg');
    width: 100vw;
    height: 30vh;
}

.committees-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    margin: 3rem auto;
    max-width: 80%;
    text-align: center;
}

.category-card {
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    height: 100%;
    width: 100%;
    padding-top: 1rem;
}   

.category-card:nth-child(3), .feedback-card {
    grid-column: 1 / span 2; 
    justify-self: center;
}

.feedback-card {
    width: 50vw;
}

.category-content {
    display: flex;
    flex-wrap: wrap;
}    

.committee-content {
    min-width: fit-content;
    padding: 1.5rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column; 
    align-items: center;
    justify-content: center;
}

.committee-content:hover {
    color: var(--color-secondary);
    text-decoration: underline;
}

.committee-img {
    height: 40vh;
}

@media screen and (max-width: 770px) {
    .committees-container {
        display: flex;
        flex-direction: column;
        max-width: 85vw;
    }

    .committee-img {
        height: 30vh;
    }

    .feedback-card {
        margin: auto;
        width: 70vw;
    }
}