.home {
    display: flex;
    top: 0;
    flex-direction: column;
}

.home-hero {
    background-image: url('../../assets/home_background.webp');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100vw;
    height: 100vh;
}

.home_container {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    margin-top: 23vh;
    gap: 20px;
    color: var(--color-text_w);
}

.home-hero::before {
    content: "";
    position: absolute;
    width: inherit;
    height: inherit;
    background: rgba(0, 53, 148, 0.4);
}

.home-header {
    font-size: 120px;
}

.home-subheader {
    font-size: 30px;
}

.home-letter {
    margin: 5rem auto;
}

.home-title {
    font-size: 56px;
    text-align: center;
    margin-bottom: 1rem;
}

.home-letter_container {
    display: flex;
    flex-direction: column;   
    line-height: 2;
    max-width: clamp(320px, 80%, 1200px);
    margin: 0 auto;
}

.home-letter-body {
    display: flex;
    flex-direction: column;
}

.letter-paragraph {
    font-weight: 100;
    text-align: left;
    margin-bottom: 1.5rem;
}

.letter-paragraph:nth-child(2){
    margin: 1.5rem 0;
}

.ending {
    line-height: 1.5rem;
}

.subtext {
    font-size: 16px;
}


@media screen and (max-width: 770px) {
    
    .home-title {
        font-size: 34px;
    }

    .home-header {
        font-size: 110px;
    }
    
    .home-subheader {
        font-size: 22px;
    }

    .subtext {
        font-size: 12px;
    }
}