.navbar {
    padding: .5rem 1rem 0;
    background: var(--color-primary);
    color: var(--color-text_w);
    position: relative;
    top: 0px;
    z-index: 1000;
}

.mobile { 
    display: none;
}

.navbar-container {
    display: grid;
    grid-template-columns: 4fr 1fr;
    align-items: center;
}

.navbar-logo {
    margin-right: 2rem;
    width: 75px;
    height: 75px;
}

.desktop {
    display: flex;
    align-items: center;
    padding-right: 2rem;
    gap: 25px;
}

.navbar-link {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0 auto;
    font-weight: 500;
    font-size: 16px;
}

.arrow {
    position: absolute;
}

.left {
    left: 0;
    transform: translateX(-100%);
}

.right {
    right: 0;
    transform: translateX(100%);
}

.navbar-about-wrapper {
    position: relative;
}

.navbar-about-content {
    position: absolute;
    pointer-events: none;
    left: -1rem;
    top: 100%;
    width: max-content;
    text-align: left;
    padding: 1rem;
    z-index: 1;
    background-color: var(--color-primary);
    transform: translateY(-10px);
    opacity: 0;
    transition: opacity .3s ease, transform .3s ease;
}

.navbar-about-wrapper:hover .navbar-about-content{
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0);
}

.navbar-link:hover {
    text-decoration: underline;
}

.navbar-link.active {
    color: var(--color-secondary);
}

.hamburger-container {
    margin: 0 auto;
}

.hamburger{
    display: none; 
    background: none;
    border: none;
    color: var(--color-text_w);
    font-size: 2rem;
    margin: 0;
    padding: 0;
    border-radius: 0;
}

.hamburger:before .hamburger:after {
    background-color: unset;
}

.hamburger:hover{
    opacity: 100%;
}

.navbar-button {
    display: flex;
}

/* HTML: <div class="loader"></div> */
.logout-loader {
    position: relative;
    padding-right: 1.5rem;
    pointer-events: none;
    width: fit-content;
    margin: 0 auto;
}

.logout-loader:after {
    content: "";
    position: absolute;
    bottom: 3px;
    right: 0;
    width: 20px;
    aspect-ratio: 4;
    background: radial-gradient(circle closest-side,var(--color-primary) 90%,#0000) 0/calc(100%/3) 100% space;
    clip-path: inset(0 100% 0 0);
    animation: l1 1s steps(4) infinite;
  }

  @keyframes l1 {to{clip-path: inset(0 -34% 0 0)}}

  .menu-loader {
    position: relative;
    padding-right: 1.5rem;
    pointer-events: none;
    width: fit-content;
    margin: 0 auto;
}

.menu-loader:after {
    content: "";
    position: absolute;
    bottom: 3px;
    right: 0;
    width: 20px;
    aspect-ratio: 4;
    background: radial-gradient(circle closest-side,var(--color-text_w) 90%,#0000) 0/calc(100%/3) 100% space;
    clip-path: inset(0 100% 0 0);
    animation: l1 1s steps(4) infinite;
  }

@media screen and (max-width: 770px) {
    .desktop {
        display: none;
    }

    .hamburger {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        background: none;
        border: none;
        cursor: pointer;
        width: 40px;
        height: 30px;
        z-index: 10000;
    }

    .hamburger span {
        display: block;
        width: 100%;
        height: 3px;
        background-color: var(--color-text_w);
        border-radius: 5px;
        transition: .9s;
    }

    .hamburger.open span:nth-child(1) {
        transform: translateY(12px) rotate(315deg);
    }

    .hamburger.open span:nth-child(2){
        opacity: 0;
    }

    .hamburger.open span:nth-child(3) {
        transform: translateY(-15px) rotate(-315deg);
    }

    .mobile-background {
        background-color: var(--color-primary);
        height: 100vh;
        position: absolute;
        top: 100%;
        width: 100%; 
        right: 0;
        left: 0;
        z-index: 999;
    }

    .mobile-background.in {
        animation: nav-slideIn .5s forwards;
    }

    .mobile-background.out {
        animation: nav-slideOut .5s forwards;
    }

    @keyframes nav-slideIn {
        0% {
            transform: translateY(100%);
        }
        100% {
            transform: translateY(0);
        }
    }

    @keyframes nav-slideOut {
        0% {
            transform: translateY(0);
        }
        100% {
            transform: translateY(100%);
        }
    }

    .mobile {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding: 1rem;
        font-size: 34px;
        text-align: center;
      
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
      
        transition: transform 0.3s ease-in-out;
      }

      .main-menu {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 2;
      }
      
      .about-menu {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        z-index: 1;
      }
      
      /* MAIN MENU TRANSITIONS */
      
    .main-menu.animate-slide-left {
        animation: slideLeft 0.3s forwards;
    }
    
    .main-menu.animate-slide-in-left {
        animation: slideInLeft 0.3s forwards;
    }
    
    .about-menu.animate-slide-right {
        animation: slideRight 0.3s forwards;
    }
    
    .about-menu.animate-slide-out-right {
        animation: slideOutRight 0.3s forwards;
    }

    @keyframes slideLeft {
        from { transform: translateX(0); }
        to { transform: translateX(-100%); }
    }
    
    @keyframes slideRight {
        from { transform: translateX(100%); }
        to { transform: translateX(0); }
    }
    
    @keyframes slideOutRight {
        from { transform: translateX(0); }
        to { transform: translateX(100%); }
    }
    
    @keyframes slideInLeft {
        from { transform: translateX(-100%); }
        to { transform: translateX(0); }
    }
      
    .navbar-link:hover {
        text-decoration: none;
    }
}