.admin-panel {
    position: fixed;
    top: 50%;
    left: 50%;
    background: var(--color-text_w);
    border-radius: 10px;
    padding: 1rem;
    border: 1px var(--color-primary), solid;
    z-index: 10000;
    width: 400px;

    display: flex;
    flex-direction: column;

}

@keyframes slideDownOpen {
    0% {
        transform: translate(-50%, -50%) scaleY(0);
    }
    100% {
        transform: translate(-50%, -50%) scaleY(1);
    }
}

@keyframes slideDownClose {
    0% {
        transform: translate(-50%, -50%) scaleY(1);
    }
    100% {
        transform: translate(-50%, -50%) scaleY(0);
    }
}

.admin-panel.opening {
    animation: slideDownOpen 1s forwards;
}

.admin-panel.closing {
    animation: slideDownClose 1s backwards;
}

.modal-footer {
    display: flex;
    align-items: center;
    gap: 2rem;
}

.date-selector {
    background-color: var(--color-primary);
    color: var(--color-text_w);
    border-radius: 5px;
    width: auto;
    margin: 0;
    cursor: text;
}

input[type="date"]::-webkit-calendar-picker-indicator {
    background-color: var(--color-text_w);/* Custom color for the icon */
    border-radius: 50%;
    cursor: pointer;
}

.fileUpload-button {
    background-color: var(--color-text_g);
    width: fit-content;
    padding: .5rem;
    border-radius: 5px;
    cursor: pointer;
}

.admin-schedule-content {
    display: grid;
    grid-template-columns: 0fr 1fr .5fr;
    align-items: center;
    justify-items: end;
    gap: .5rem;
}

.admin-schedule-header {
    background: var(--color-primary);
    color: var(--color-text_w);
    padding: 1rem .5rem;
    border-radius: 5px;
}

.admin-schedule-events {
    grid-template-columns: none;
    margin: 1rem 0;
}

.admin-schedule-container {
    display: flex;
    flex-direction: column;
    width: 75%;
    margin: 1rem auto;
}

.admin-schedule-info-container {
    background-color: #F9FAFB;
    border-left: 5px var(--color-primary) solid;
    border-radius: 10px;
    width: 100%;
    padding: 1.25rem .5rem;
}

.admin-schedule-grid {
    position: relative;
    display: grid;
    align-items: center;
    justify-items: end;
    margin-right: 1rem;
}

.admin-date {
    grid-template-columns: auto auto auto auto 2fr;
    gap: .5rem;
}

.info {
    grid-template-columns: auto auto 5fr 1fr;
    gap: 1rem;
}

.admin-event {
    color: var(--color-primary);
}

.admin-time {
    color:var(--color-text_b);
    position: relative;
    opacity: 80%;
    text-transform: uppercase;
}

.admin-time::after {
    content: "";
    position: absolute;
    bottom: 7.5px;
    left: -9px;
    width: 5px;
    height: 5px;
    border-radius: 360px;
    background: var(--color-text_b);
}

.admin-date-title {
    color: var(--color-text_w);
    font-size: 18px;
}

.modal-container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #F9FAFB;
    border-radius: 5px;
    border-top: 5px var(--color-primary) solid;
    text-align: left;
    padding: 1rem;
    z-index: 100;
    backdrop-filter: brightning(50%);
}

.modal-button {
    width: -webkit-fill-available;
}

.schedule-input {
    width: 300px;
    padding: 1.25rem .5rem;
    border: 1px var(--color-primary) solid;
    border-radius: 10px;
    background-color: var(--color-text_w);
    color: var(--color-primary);
    margin-bottom: 1rem;
}

.button-container {
    display: flex;
    justify-content: flex-end;
    gap: .5rem;
}

.admin-button {
    padding: 0;
    margin: 0;
    border: none;
    background: none;  
    cursor: pointer;  
}

.admin-button:hover {
    transform: none;
}

.select-date-dropdown {
    position: absolute;
    top: 45px; /* Adjust based on your layout */
    left: 10px; /* Adjust based on your layout */
    background-color: var(--color-text_w);
    color: var(--color-text_p);
    border: 1px solid var(--color-primary);
    border-radius: 5px;
    max-height: 200px;
    overflow-y: auto;
    width: 220px; /* Match your select width */
  }
  
  .option-group {
    border-bottom: 1px solid #eee;
    padding: 4px 0;
  }
  
  .select-date-dropdown .option {
    padding: .75rem 1rem;
    cursor: pointer;
    transition: background 0.2s ease;
    grid-template-columns: auto auto;
    justify-content: center;
    margin: 0;
  }
  
  .select-date-dropdown .option:hover {
    background-color: #f5f5f5;
  }
  
  .select-date-dropdown .option p {
    margin: 0;
    font-size: 14px;
  }

.date-changer {
    color: var(--color-text_w);
}
  

.calender {
    color: var(--color-secondary);
}

.edit-menu-buttons {
    position: absolute;
    right: 1rem;
    background-color: var(--color-text_g);
    padding: .25rem;
    border-radius: 50%;
    cursor: pointer;
    z-index: 100;
}

.close {
    top: 1rem;
    cursor: pointer;
}

.plus {
    top: 3rem;
}

.add {
    background: var(--color-secondary);
    color: var(--color-primary);
    border-radius: 50%;
    padding: .25rem;
}

.arrow-up {
    top: 5rem;
}

.arrow-down {
    top: 7rem;    
}

.edit {
    color: var(--color-primary);
}

.delete {
    color: var(--color-secondary);
}

.cancel {
    background-color: var(--color-text_w);
    border: 1px var(--color-primary) solid;
    border-radius: 5px;
    color: var(--color-primary);
    padding: .5rem
}

.menu-button {
    margin: 0 auto;
    width: 230px;
}

.menu-grid {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.menu-title {
    margin-bottom: 2rem;
    position: relative;
}

.menu-title:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -.5rem;
    width: 50%;
    height: 3px;
    border-radius: 5px;
    background-color: var(--color-secondary);
}

.select-person {
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 1rem;  
    cursor: pointer;
}

.complete-screen {
    position: fixed;
    top: 0;
    left: 0;
    border-radius: 5px;
    height: 100%;
    width: 100%;
    background-color: #4BB543;
    color: var(--color-text_w);

    display: flex;
    align-items: center;
    justify-content: center;
    
    animation: showComplete 2s forwards;
}

@keyframes showComplete {
    0% {
        opacity: 0.2;
    }
    100% {
        opacity: 1;
    }
}

@media screen and (max-width: 770px) {
    .admin-panel {
        width: 350px;
    }    
}