.contact_container {
    margin: 0px auto;
    font-size: 20px;
    text-align: left;
    width: 60vw;
}

.required{
    color: var(--color-secondary);
}

.contact-body_title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
}

.contact_content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.contact_input {
    min-width: 25vw;
}

@media screen and (max-width: 770px) {

    .contact_container {
        font-size: 14px;
    }

    .contact_content {
        gap: 1rem;
    }


}