.admin-register-data {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 4rem;
    gap: 4rem;
}

.admin-data {
    display: flex;
    flex-direction: row;
    text-align: center;
    gap: 1rem;
    border-bottom: 2px solid var(--color-secondary);
    padding-bottom: 2rem;
}

.data-title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 1rem;
}

.login-form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 5em 0;

}

.login-container {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    border: 1px black solid;
    border-radius: 10px;
}

.form-title {
    text-align: center;
    margin-bottom: .5rem;
}

.form-subtitle {
    margin-bottom: 2rem;
}

.form-logo {
    width: 100px;
    margin: 0 auto 1rem;
}

.form-header {
    font-size: 18px;
    font-weight: 600;
}

.form-input {
    width: 25vw;
    padding: 1rem;
    position: relative;
}

.form-button {
    width: 100%;
    margin-top: 2rem;
}

.password::after {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    cursor: pointer;
}

/* HTML: <div class="loader"></div> */
.login-loader {
    width: 30px;
    --b: 8px; 
    aspect-ratio: 1;
    border-radius: 50%;
    padding: 1px;
    background: conic-gradient(#0000 10%,var(--color-secondary)) content-box;
    -webkit-mask:
      repeating-conic-gradient(#0000 0deg,#000 1deg 20deg,#0000 21deg 36deg),
      radial-gradient(farthest-side,#0000 calc(100% - var(--b) - 1px),#000 calc(100% - var(--b)));
    -webkit-mask-composite: destination-in;
            mask-composite: intersect;
    animation:l4 1s infinite steps(10);
    margin: 0 auto;
  }
  @keyframes l4 {to{transform: rotate(1turn)}}

.page-button.load {
    pointer-events: none;
}

.add-event {
    background: var(--color-primary);
    color: var(--color-secondary);
    padding: .5rem;
    border-radius: 5px;
}

@media screen and (max-width: 770px) {
    .admin-data {
        flex-direction: column;
    }
    
    .form-input {
        width: 75vw;
    }
}